@import "../../inc/vars";
.acrobits_hero {
	padding: 86px 0 55px 0;
	position: relative;
	.acroPopup {
		position: absolute;
		top: 0;
		left: 50%;
		width: 536px;
		transform: translateX(-50%);
		max-width: calc(100% - 32px);
		padding: 8px 40px 8px 12px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-radius: 4px;
		border: 1px solid var(--gray-gray-100, #ECEDEE);
		background: var(--gray-gray-50, #F6F7F7);

		/* button / purple */
		box-shadow: 0px 1.127px 4.132px 0px rgba(112, 72, 199, 0.40), 0px 9px 33px 0px rgba(112, 72, 199, 0.20);
		&__text {
			@include pop(600, 12, 16px);
			color: #29292A;
			margin-right: 10px;
		}
		&__button {
			@include pop(500, 12, 1);
			color: #fff;
			display: inline-block;
			padding: 12px 20px;
			background: $green;
			border-radius: 6px;
			text-decoration: none;
			transition: .3s;
			&:hover {
				background: #278763;
			}
		}
		&__close {
			position: absolute;
			right: 8px;
			width: 24px;
			height: 24px;
			cursor: pointer;
		}
	}
	@media (min-width: 768px){
		padding: 48px 0;
	}
	.title-box {
		padding: 16px 30px;
		text-align: center;
		@media(min-width: 1024px){
			padding: 24px 30px;
			width: 50%;
			margin-left: auto;
			margin-right: auto;
		}
	}
	&__subtitle {
		@include pop(400, 23, 1.36);
		color: $purple;
		letter-spacing: -0.46px;
		margin-bottom: 16px;
	}
	&__title {
		@include pop(400, 44, 1.24);
		letter-spacing: -1.06px;
		margin-bottom: 12px;
		background: var(--grad-title, linear-gradient(180deg, #2D3442 0%, #6C7689 100%));
		background-clip: text;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		@media (min-width: 1024px){
			@include pop(400, 53, 1.24);
		}
	}
	&__description {
		color: #4F4B58;
		@include fig(400, 20, 1.24);
	}
	.acrobits_hero__slides {
		position: relative;
		display: flex;
		align-items: flex-end;
		justify-content: center;
		padding: 0 0 40px 0;
		@media (min-width: 1024px){
			padding: 40px 0;
		}
		&:before {
			content: '';
			display: block;
			width: 592px;
			height: 534px;
			border-radius: 592px;
			background: rgba(102, 37, 133, 0.20);
			filter: blur(170px);
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			animation: none;
		}
		&:after {
			content: '';
			display: block;
			width: 390px;
			height: 352px;
			border-radius: 390px;
			background: rgba(56, 36, 100, 0.30);
			filter: blur(100px);
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			z-index: 2;
		}
		.slick-list {
			//overflow-x: visible !important;
			width: 100% !important;
			z-index: 3;
			.slick-track {
				display: flex !important;
				align-items: flex-end;
				flex-wrap: wrap;
			}
		}
		.slick-dots {
			position: absolute;
			bottom: 0;
			width: 100%;
			text-align: center;
			li {
				display: inline-block;
				button {
					font-size: 0;
					width: 12px;
					height: 12px;
					border-radius: 100%;
					background: #B5B7B9;
					transition: .3s;
					margin: 0 5px;
				}
				&.slick-active {
					button {
						width: 16px;
						height: 16px;
						background: $purple;
					}
				}
			}
		}
		.slick-arrow {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			font-size: 0;
			width: 24px;
			height: 24px;
			display: block;
			svg path {
				transition: .3s;
			}
			&.slick-disabled {
				path {
					fill: #B5B7B9;
				}
			}
			&.slick-prev {
				margin-left: -24px;
			}
			&.slick-next {
				margin-right: -24px;
			}
		}
		.slide {
			position: relative;
			width: auto;
			&.slick-cloned {
				opacity: 0;
			}
			.slide-image {
				img {
					max-width: 100%;
					height: auto;
				}
			}
			.trustpilot {
				position: absolute;
				border-radius: var(--Medium, 4px);
				padding: 10px;
				background: var(--white, #FFF);
				/* shadow / thumbs */

				box-shadow: 0px 2.767px 2.214px 0px rgba(0, 0, 0, 0.16), 0px 6.65px 5.32px 0px rgba(0, 0, 0, 0.12), 0px 12.522px 10.017px 0px rgba(0, 0, 0, 0.10), 0px 22.336px 17.869px 0px rgba(0, 0, 0, 0.08), 0px 41.778px 33.422px 0px rgba(0, 0, 0, 0.06), 0px 100px 80px 0px rgba(0, 0, 0, 0.04);
				@media(min-width: 1024px){
					display: inline-block;
					padding: 12px;
				}
				img {
					width: 184px;
					@media(min-width: 1024px){
						width: 215px;
					}
				}
			}
			.slide-logo {
				bottom: 13px;
				text-align: center;
				img {
					margin: 0 auto;
				}
				@media(min-width: 1024px){
					position: absolute;
				}
			}
		}
		.slide1 {
			text-align: right;
			padding: 0 55px;
			z-index: 3;
			@media(min-width: 1024px){
				right: -30px;
				padding: 0;
			}
			.slide-image {
				position: relative;
				&:before {
					content: '';
					display: block;
					width: 300px;
					height: 300px;
					border-radius: 50%;
					position: absolute;
					background: rgba(102, 37, 133, 0.2);
					top: calc(50% - 150px);
					left: calc(100% - 150px);
					z-index: 1;
					filter: blur(40px);
					@media(min-width: 1024px){
						display: none;
					}
				}
				img {
					width: 583px;
					max-width: 100%;
					height: auto;
					position: relative;
					z-index: 2;
				}
				@media(min-width: 1024px){
					width: auto;
					filter: none;
				}
			}
			.trustpilot {
				left: 50%;
				bottom: 120px;
				transform: translateX(-50%);
				z-index: 2;
				@media(min-width: 1024px){
					left: -43px;
					bottom: 100px;
					transform: translateX(0);
				}
			}
			.slide-logo {
				img {
					width: 146px;
					@media(min-width: 1024px){
						width: 212px;
					}
				}
				@media(min-width: 1024px){
					left: -80px;
					transform: none;
				}
			}
		}
		.slide2 {
			text-align: left;
			padding: 0 55px;
			@media(min-width: 1024px){
				left: -30px;
				padding: 0;
			}
			.slide-image {
				img {
					width: 460px;
					max-width: 100%;
					height: auto;
				}
				@media(min-width: 1024px){
					width: auto;
				}
			}
			.trustpilot {
				right: auto;
				left: 44%;
				bottom: 90px;
				transform: translateX(-50%);
				@media(min-width: 1024px){
					left: auto;
					right: -87px;
					bottom: 100px;
					transform: translateX(0);
				}
			}
			.slide-logo {
				img {
					width: 110px;
					@media(min-width: 1024px){
						width: 160px;
						margin-right: auto;
					}
				}
				@media(min-width: 1024px){
					right: -50px;
					transform: none;
				}
			}
		}
		@media (max-width: 767px){
			&:not(.slick-initialized){
				.slide {
					opacity: 0;
				}
			}
		}
	}
}

@keyframes bounce {
	0% {
		transform: scale(1);
	}
	50% {
		transform: scale(5);
	}
	100% {
		transform: scale(1);
	}
}